"use client";

import * as React from "react";
import { useQuery } from "@tanstack/react-query";
import type { ColumnDef } from "@tanstack/react-table";
import { api } from "@/lib/api";
import { DataGrid } from "@/components/tables/data-grid";
import { colDateTime, colMono, hashShort } from "@/components/tables/column-helpers";
import { ExecutionScoreBadge } from "@/components/ui/ExecutionScoreBadge";
import { AlertCard } from "@/components/alerts/AlertCard";

type Severity = "INFO" | "WARN" | "CRITICAL";

type AlertRow = {
  id: string;
  created_at?: string | null;
  severity?: Severity | null;
  category?: string | null;
  title?: string | null;
  reason?: string | null;
  trade_id?: string | null;
  wallet_id?: string | null;
  creator_pubkey?: string | null;
  execution_score?: number | null;
  resolved?: boolean | null;
};

type AlertsQueryFnData = {
  items: any[];
  page: number;
  page_size: number;
  total: number;
};

function clampSeverity(s: unknown): Severity {
  const v = String(s ?? "INFO").toUpperCase();
  if (v === "CRITICAL" || v === "WARN") return v as Severity;
  return "INFO";
}

function normalizeAlerts(rows: any[]): AlertRow[] {
  if (!Array.isArray(rows)) return [];
  return rows.map((a) => ({
    id: String(a.id ?? a.alert_id ?? Math.random().toString(36).slice(2)),
    created_at: a.created_at ?? a.ts ?? a.time ?? null,
    severity: clampSeverity(a.severity ?? a.level),
    category: a.category ?? null,
    title: a.title ?? null,
    reason: a.reason ?? null,
    trade_id: a.trade_id ?? null,
    wallet_id: a.wallet_id ?? null,
    creator_pubkey: a.creator_pubkey ?? null,
    execution_score: a.execution_score ?? a.exec_score ?? null,
    resolved: a.resolved ?? null,
  }));
}

function useColumns(): ColumnDef<AlertRow, any>[] {
  return React.useMemo<ColumnDef<AlertRow, any>[]>(() => [
    colDateTime<AlertRow>("created_at", "Time"),
    {
      id: "severity",
      header: "Severity",
      accessorKey: "severity",
      cell: (ctx) => {
        const v = (ctx.getValue() as Severity | null) ?? "INFO";
        const tone =
          v === "CRITICAL"
            ? "bg-rose-900/40 text-rose-300"
            : v === "WARN"
            ? "bg-amber-900/40 text-amber-300"
            : "bg-blue-900/40 text-blue-300";
        return <span className={`px-2 py-0.5 rounded text-xs ${tone}`}>{v}</span>;
      },
      enableSorting: true,
    },
    colMono<AlertRow>("category", "Category"),
    {
      id: "title",
      header: "Title / Reason",
      cell: (ctx) => {
        const r = ctx.row.original;
        return (
          <div className="min-w-[280px]">
            <div className="font-medium truncate">{r.title ?? r.reason ?? "—"}</div>
            {r.reason && r.title && (
              <div className="text-xs text-muted-foreground truncate">{r.reason}</div>
            )}
          </div>
        );
      },
    },
    {
      id: "exec",
      header: "Exec",
      accessorKey: "execution_score",
      cell: (ctx) => (
        <ExecutionScoreBadge
          score={typeof ctx.getValue() === "number" ? (ctx.getValue() as number) : null}
        />
      ),
      enableSorting: true,
    },
    {
      id: "entities",
      header: "Entities",
      cell: (ctx) => {
        const r = ctx.row.original;
        return (
          <div className="text-xs">
            {r.wallet_id && <span className="font-mono">wallet {hashShort(r.wallet_id)} </span>}
            {r.creator_pubkey && (
              <>
                · <span className="font-mono">{hashShort(r.creator_pubkey)}</span>
              </>
            )}
          </div>
        );
      },
    },
  ], []);
}

export default function AlertsPage() {
  const [q, setQ] = React.useState("");
  const [severity, setSeverity] = React.useState<"ALL" | Severity>("ALL");
  const [resolved, setResolved] = React.useState<"ALL" | "YES" | "NO">("ALL");
  const [view, setView] = React.useState<"cards" | "table">("cards");

  // Build params for the backend
  const params = React.useMemo(() => {
    const p = new URLSearchParams();
    if (q) p.set("q", q);
    if (severity !== "ALL") p.set("severity", severity);
    if (resolved === "YES") p.set("resolved", "true");
    if (resolved === "NO") p.set("resolved", "false");
    p.set("page", "1");
    p.set("page_size", "300");
    return p;
  }, [q, severity, resolved]);

  // ✅ unified with updated api.getAlerts (verbatim passthrough)
  const {
    data: alerts = [],
    isLoading,
    error,
    refetch,
  } = useQuery<AlertsQueryFnData, Error, AlertRow[]>({
    queryKey: ["alerts", q, severity, resolved],
    queryFn: () => api.getAlerts(params),
    select: (res) => normalizeAlerts(res.items ?? []),
    placeholderData: { items: [], page: 1, page_size: 300, total: 0 },
    refetchInterval: 5000,
    staleTime: 4000,
    gcTime: 60000,
  });

  const columns = useColumns();

  return (
    <div className="p-4 max-w-[1200px] mx-auto">
      <div className="flex items-center justify-between mb-3">
        <h1 className="text-xl font-semibold">Alerts Inbox</h1>
        <div className="flex items-center gap-2">
          <button
            className={`px-2 py-1 rounded border ${view === "cards" ? "bg-muted" : ""}`}
            onClick={() => setView("cards")}
          >
            Cards
          </button>
          <button
            className={`px-2 py-1 rounded border ${view === "table" ? "bg-muted" : ""}`}
            onClick={() => setView("table")}
          >
            Table
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="mb-3 grid gap-2 md:grid-cols-3">
        <input
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          placeholder="Search title/reason…"
          value={q}
          onChange={(e) => setQ(e.target.value)}
        />
        <select
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          value={severity}
          onChange={(e) => setSeverity(e.target.value as any)}
        >
          <option value="ALL">All severities</option>
          <option value="INFO">Info</option>
          <option value="WARN">Warn</option>
          <option value="CRITICAL">Critical</option>
        </select>
        <select
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          value={resolved}
          onChange={(e) => setResolved(e.target.value as any)}
        >
          <option value="ALL">All statuses</option>
          <option value="NO">Unresolved</option>
          <option value="YES">Resolved</option>
        </select>
      </div>

      {isLoading && <div>Loading…</div>}
      {error && (
        <div className="text-destructive mb-3">
          Failed to load. <button className="underline" onClick={() => refetch()}>Retry</button>
        </div>
      )}

      {view === "cards" ? (
        <div className="grid gap-3">
          {alerts.length === 0 && <div className="text-sm text-muted-foreground">All clear.</div>}
          {alerts.map((a) => (
            <AlertCard key={a.id} a={a} />
          ))}
        </div>
      ) : (
        <DataGrid<AlertRow>
          data={alerts}
          columns={columns}
          pageSize={25}
          initialSorting={[{ id: "created_at", desc: true }]}
          emptyLabel="No alerts."
        />
      )}
    </div>
  );
}
